<template>
  <Icon
    :class="{ 'pin-check_active': value }"
    class="comment-check"
    :name="value ? 'comment-checked' : 'comment-check'"
    @click="toggle"
  />
</template>

<script>
import { useModel } from '@/shared/utils/useModel'
import Icon from '@/shared/components/Icon/Icon'

export default {
  name: 'CommentCheck',
  components: { Icon },
  emits: ['update:modelValue'],
  props: {
    modelValue: {
      type: Boolean,
      required: true
    }
  },
  setup (props, { emit }) {
    const { value } = useModel(props, emit)

    const toggle = () => {
      value.value = !value.value
    }

    return {
      value,
      toggle
    }
  }
}
</script>

<style lang="scss">
.comment-check {
  cursor: pointer;
}
</style>
